html, body {
    font-size: 18px;
}

body {
    /*background: #3E5151;  /* fallback for old browsers */
    /*background: -webkit-linear-gradient(to top, #DECBA4, #3E5151);  /* Chrome 10-25, Safari 5.1-6 */
    /*background: linear-gradient(to top, #DECBA4, #3E5151); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: #D3959B;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #BFE6BA, #D3959B);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #BFE6BA, #D3959B); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

    height: 100vh;
}

form {
    margin: 0 auto;
    width: 330px;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: bold;
}
label {
    padding: 0.25rem;
    font-size: 14px;
}
textarea {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 14px;
    font-weight: bold;
    color: #545454;
}

input[type="submit"] {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 14px;
    border: none;
    background-color: #c85c66;
    color: white;
    padding: 0.25rem 1rem;
    border-radius: 7px;
}

.form-wrapper{
    background-color: #f8f5f5;
    width: 375px;
    margin: 4rem auto;
    padding: 1.5rem;
    color: #545454;
    border-radius: 15px;
}
.form-title {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 28px;
    font-weight: bold;
    text-align: center;
    color: white;
    background-color: #c85c66;
    padding: 0.5rem 1.5rem;
    margin: -1.5rem;
    margin-bottom: 1.5rem;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
}
.textview1 {
    margin-top: 2rem;
}
.textview2 {
    margin-top: 2.5rem;
}
.field-submit {
    text-align: right;
    margin-top: 2rem;
}
